import 'bootstrap/dist/css/bootstrap.min.css';
import './App.css';
import Container from 'react-bootstrap/Container';
import Row from 'react-bootstrap/Row';
import Col from 'react-bootstrap/Col';
import Image from 'react-bootstrap/Image';
import Navbar from 'react-bootstrap/Navbar';
import { useState } from 'react';
import { AiOutlineHeart } from "react-icons/ai";
import { useEffect } from 'react';
import { useRef } from 'react';

const product = [{id: 0, image: "nike2.jpg", name: "Ruha", price: 15000, like: false}, 
               {id: 1, image: "nike2.jpg", name: "Nadrág", price: 10000, like: false},
               {id: 2, image: "nike3.jpg", name: "Ruha", price: 15000, like: false},
               {id: 3, image: "nike4.jpg", name: "Ruha", price: 15000, like: false},
               {id: 4, image: "nike6.jpg", name: "Ruha", price: 15000, like: false},
               {id: 5, image: "nike6.jpg", name: "Ruha", price: 15000, like: false},
               {id: 6, image: "nike7.jpg", name: "Ruha", price: 15000, like: false},
               {id: 7, image: "nike8.jpg", name: "Ruha", price: 15000, like: false},
               {id: 8, image: "nike9.jpg", name: "Ruha", price: 15000, like: false},]
const article =[{image: 'articleimg.jpg', text: "Lorem Ipsum is simply dummy text of the printing and typesetting industry. Lorem Ipsum has been the industry's standard dummy text ever since the 1500s, when an unknown"},
                {image: 'articleimg2.jpg'}]
const delay = 5000;

function NewProducts(){
  const [prod_name, setprod_name] = useState('');
  const [prod_price, setprod_price] = useState(0);

  function product_push(){
    product.push({id: 9, name: prod_name, price: prod_price, like: false},)
  }
  return(
    <div>
      Name: <input type='text' id="product_name" value={prod_name} onChange={e => setprod_name(e.target.value)}></input>
      Price: <input type='number' id="product_price" value={prod_price} onChange={e => setprod_price(e.target.value)}></input>
      <button type='submit' onClick={()=>{product_push()}}>Felvitel</button>
    </div>
  )
}
function Articles(){
  const [index, setIndex] = useState(0);
  const timeoutRef = useRef(null);

  function resetTimeout() {
    if (timeoutRef.current) {
      clearTimeout(timeoutRef.current);
    }
  }
  useEffect(() => {
    resetTimeout();
    timeoutRef.current = setTimeout(
      () =>
        setIndex((prevIndex) =>
          prevIndex === article.length - 1 ? 0 : prevIndex + 1
        ),
      delay
    );

    return () => {
      resetTimeout();
    };
  }, [index]);

  return (
      <Container>
        <Row>
          <Col xs="8">
              <div className='article_style' >
                <div className="slideshow">
                  <div className="slideshowSlider" style={{ transform: `translate3d(${-index * 100}%, 0, 0)` }}>
                    {article.map((item, index) => (
                      <div className="slide" key={index} ><Image className='product_image' src={require("./"+item.image)} alt={item.name} fluid></Image></div>
                        ))}
                      </div>

                      <div className="slideshowDots">
                        {article.map((_, idx) => (
                          <div
                            key={idx}
                            className={`slideshowDot${index === idx ? " active" : ""}`}
                            onClick={() => {
                              setIndex(idx);
                            }}
                          ></div>
                        ))}
                      </div>
                </div>
                <p className='article_text'>{article[0].text}</p>
              </div>
          </Col>
          <Col xs="2"></Col>
        </Row>
      </Container>
    
  )
}
function Mynavbar() {
  return (
    <Navbar className="bg-body-tertiary hatterszin">
      <Container>
        <Navbar.Brand href="#home">Navbar with text</Navbar.Brand>
        <Navbar.Toggle />
        <Navbar.Collapse className="justify-content-end">
          <Navbar.Text>
            Signed in as: <a href="#login">Mark Otto</a>
          </Navbar.Text>
        </Navbar.Collapse>
      </Container>
    </Navbar>
  );
}
function ProductsCard(){
    return(
      <div>
        <Container>
          <Row xs={1} md={4}>
            {product.map(item =>
              <Col key={item.id}>
                <div className='hatter'>
                  <Image className='product_image' src={require("./"+item.image)} alt={item.name} fluid></Image>
                  <p className='name'>{item.name}</p>
                  <p className='price'>{item.price} FT</p>
                  <AiOutlineHeart onClick={()=>{alert(item.id)}}></AiOutlineHeart>
                </div>
              </Col>
            )}
          </Row>
        </Container>
      </div>
    )
}

export default function App() {
  return (
    <>
    <div className='backgroundImages'>
        <Container>
          <Row>
            <Col md={{offset: 0 }}><Image className='backgroundImagesContent1' src={require("./leaves.png")}  ></Image></Col>
            <Col md={{span: 2, offset: 0 }}> <Image className='backgroundImagesContent2' src={require("./leaves.png")}  ></Image> </Col>
          </Row>
          <Row>
            <Col md={{offset: 2}}><Image className='backgroundImagesContent2' src={require("./leaves.png")}  ></Image></Col>
          </Row>
        </Container>
      
      </div>
    <div className='background'>
      
      <Mynavbar></Mynavbar>
      
      <Articles></Articles>
        <div style={{display: 'flex', flexDirection: 'row', alignItems: 'center'}}>
          <div style={{flex: 1, height: '1px', backgroundColor: 'black'}} />
            <div>
              <p style={{width: '70px', textAlign: 'center'}}>Example</p>
            </div>
          <div style={{flex: 1, height: '1px', backgroundColor: 'black'}} />
        </div>
      <ProductsCard></ProductsCard>
      <NewProducts></NewProducts>
    </div>
    </>
  );
}


